import React from "react"
import Head from '../components/head'
import Layout from "../components/layout"
import projectStyle from '../styles/project.module.scss'

import { Link, graphql, useStaticQuery } from "gatsby"

function ProjectPage() {

    const data = useStaticQuery(graphql`
        query{
            allMarkdownRemark{
                edges{
                    node{
                        frontmatter{
                            title
                            date
                        }
                        fields{
                            slug
                        }
                    }
                }
            }
        }
    `)

    return (
        <Layout>
            <Head title="Projects" />
            <div className={projectStyle.about}>
                <h1 style={{ color: '#64DEF7' }}>Featured Projects</h1>
            </div>
            <ol className={projectStyle.posts}>
                {data.allMarkdownRemark.edges.map((edge) => {
                    return(
                        <li className = {projectStyle.post}>
                            <Link to = {`/project/${edge.node.fields.slug}`}>
                            <h2>{edge.node.frontmatter.title}</h2>
                            <p>{edge.node.frontmatter.date}</p>
                            </Link>
                        </li>
                    )
                })}
            </ol>
        </Layout>
    )
}

export default ProjectPage